<template>
  <div id='main'>
    <div class="content">
      <!-- 搜索 -->
      <div class="choose">
        <div style="padding:15px 15px;">
          <el-form :inline="true" :model="search">
            <el-form-item label="课程名称">
              <el-input v-model="search.courseName" placeholder="请输入课程名称" clearable></el-input>
            </el-form-item>
            <el-form-item label="学院">
              <el-select v-model="search.instituteId" placeholder="请选择学院" clearable>
                <el-option v-for="item in consts.instituteList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="sumitQuery">查询</el-button>
            </el-form-item>
            <el-form-item v-if="user.roleId === 1">
              <el-button type="success" @click="add">新增</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!-- 新增 -->
      <el-dialog :visible.sync="formVisible" title="课程信息" width="33%">
        <el-form ref="form" :model="form" :rules="formRules" label-width="120px">
          <el-form-item label="课程名称" prop="courseName">
            <el-input v-model="form.courseName" placeholder="请输入课程名"></el-input>
          </el-form-item>
          <el-form-item label="学院" prop="instituteId">
            <el-select v-model="form.instituteId" placeholder="请选择学院" @change="changeSpeciality">
              <el-option v-for="item in consts.instituteList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="专业" prop="specialityId">
            <el-select v-model="form.specialityId" placeholder="请选择专业" :disabled="!/\s*/.test(form.instituteId)" multiple clearable>
              <el-option v-for="item in specialityList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="formVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 表格 -->
      <v-table :tableData="tableData" :columns="columns" :pagination="pagination" @handleTableChange="find" :otherHeight="otherHeight"></v-table>
    </div>
    <div style="clear:both"></div>
  </div>

</template>
<script>
import VTable from '../components/VTable.vue'
import consts from '../assets/utils/consts.js'

export default {
  name: 'Course',
  components: {
    VTable,
  },
  data() {
    return {
      consts,
      formVisible: false,
      specialityList: [],
      otherHeight: 0,
      search: {
        courseName: '',
        instituteId: '',
      },
      form: {
        courseId: '',
        courseName: '',
        instituteId: '',
        specialityId: [],
      },
      formRules: {
        courseName: { required: true, message: '请输入课程名', trigger: 'blur' },
        instituteId: { required: true, message: '请选择学院', trigger: 'blur' },
        specialityId: { required: true, message: '请选择专业', trigger: 'blur' },
      },
      columns: [
        { prop: 'courseId', label: '课程编号' },
        { prop: 'courseName', label: '课程名' },
        {
          prop: 'instituteId',
          label: '学院',
          format: row => `<span>${(row.instituteId && this.mapToInstitute(row.instituteId).name) || ''}</span>`,
        },
        {
          prop: 'specialityId',
          label: '专业',
          format: row =>
            `<span>${(row.specialityId && this.mapToMultipleSpecialityName(row.instituteId, row.specialityId)) ||
              ''}</span>`,
        },
        {
          prop: '',
          label: '操作',
          render: row => {
            if (this.user.roleId === 1) {
              const tempEdit = () => {
                this.modify(row)
              }
              const tempDelete = () => {
                this.delete(row.courseId)
              }
              return [
                <el-button size="small" type="success" onClick={tempEdit} plain>
                  编辑
                </el-button>,
                <el-button size="small" type="danger" onClick={tempDelete} plain>
                  删除
                </el-button>,
              ]
            }
            return [<span />]
          },
        },
      ],
      pagination: {
        current: 1,
        size: 10,
        total: 0,
        sidx: 1,
        sord: 'asc',
      },
      tableData: [],
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
  },
  mounted() {
    this.find()
  },
  methods: {
    // 查
    find(pagination) {
      this.pagination = { ...this.pagination, ...pagination }
      this.$get('api/course/course', this.pagination).then(res => {
        if (res && res.data) {
          const { data } = res
          this.tableData = data.pageRecords
          this.pagination.total = data.recordAmount
        }
      })
    },
    // 增
    add() {
      this.resetForm()
      this.formVisible = true
    },
    // 改
    modify(row) {
      this.initForm(row)
      this.formVisible = true
    },
    // 删
    delete(id) {
      this.$confirm('确定删除课程?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.$delete(`api/course/course/${id}`).then(() => {
            this.find()
          })
        })
        .catch(() => {})
    },
    sumitQuery() {
      this.find(this.search)
    },
    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$post('api/course/course', { ...this.form, specialityId: this.form.specialityId.join(',') }).then(() => {
            this.find()
            this.formVisible = false
          })
        }
        return valid
      })
    },
    resetForm() {
      this.passwordChangable = true
      this.$refs.form && this.$refs.form.clearValidate()
      Object.keys(this.form).forEach(key => {
        this.form[key] = ''
      })
      this.form.courseId = null
      this.form.specialityId = []
    },
    initForm(row) {
      this.passwordChangable = false
      this.$refs.form && this.$refs.form.clearValidate()
      this.changeSpeciality(row.instituteId)
      Object.keys(this.form).forEach(key => {
        this.form[key] = row[key]
      })
      this.form.specialityId = row.specialityId.split(',')
    },
    changeSpeciality(instituteId) {
      this.specialityList = this.mapToInstitute(instituteId).specialityList
      this.form.specialityId = []
    },
    mapToRole(roleId) {
      return this.consts.roleList.find(item => item.id === roleId)
    },
    mapToInstitute(instituteId) {
      return this.consts.instituteList.find(item => item.id === instituteId)
    },
    mapToSpeciality(instituteId, specialityId) {
      return this.mapToInstitute(instituteId).specialityList.find(item => item.id === specialityId)
    },
    mapToMultipleSpecialityName(instituteId, specialityId) {
      return specialityId
        .split(',')
        .map(item => this.mapToSpeciality(instituteId, item).name)
        .join(',')
    },
  },
}
</script>

<style>
</style>



