// 菜单配置
// headerMenuConfig：头部导航配置
// asideMenuConfig：侧边导航配置

const headerMenuConfig = []

const asideMenuConfig = [
  {
    path: '/',
    name: '用户管理',
    icon: 'el-icon-menu',
    role: [1],
    children: [
      {
        path: 'student',
        name: '学生信息管理',
        role: [1],
      },
      {
        path: 'teacher',
        name: '教师信息管理',
        role: [1],
      },
      {
        path: 'admin',
        name: '管理员信息管理',
        role: [1],
      },
    ],
  },
  {
    path: '/',
    name: '课程信息管理',
    icon: 'el-icon-menu',
    role: [1, 2, 3],
    children: [
      {
        path: 'course',
        name: '课程管理',
        role: [1, 2],
      },
      {
        path: 'courseArrange',
        name: '排课管理',
        role: [1, 2],
      },
      {
        path: 'courseElection',
        name: '选课管理',
        role: [3],
      },
      {
        path: 'courseSchedule',
        name: '课程管理',
        role: [2, 3],
      },
    ],
  },
]

export { headerMenuConfig, asideMenuConfig }



