<template>
  <div id='main'>
    <div class="content">
      <!-- 搜索 -->
      <div class="choose">
        <div style="padding:15px 15px;">
          <el-form :inline="true" :model="search">
            <el-form-item label="学号">
              <el-input v-model="search.username" placeholder="请输入学号" clearable></el-input>
            </el-form-item>
            <el-form-item label="姓名">
              <el-input v-model="search.realName" placeholder="请输入姓名" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="sumitQuery">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="success" @click="add">新增</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!-- 新增 -->
      <el-dialog :visible.sync="formVisible" title="学生信息" width="33%">
        <el-form ref="form" :model="form" :rules="formRules" label-width="120px">
          <el-form-item label="学院" prop="instituteId">
            <el-select v-model="form.instituteId" placeholder="请选择学院" @change="changeSpeciality">
              <el-option v-for="item in consts.instituteList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="专业" prop="specialityId">
            <el-select v-model="form.specialityId" placeholder="请选择专业">
              <el-option v-for="item in specialityList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学号" prop="username" v-if="passwordChangable">
            <el-input v-model="form.username" placeholder="请输入学号">
              <template slot="prepend">{{usernamePrefix}}</template>
            </el-input>
          </el-form-item>
          <!-- <el-form-item label="密码" prop="password">
            <el-input v-model="form.password" placeholder="请输入密码" type="password" :disabled="!passwordChangable"></el-input>
          </el-form-item> -->
          <!-- <el-form-item label="身份" prop="roleId">
            <el-select v-model="form.roleId" placeholder="请选择身份">
              <el-option v-for="role in consts.roleList" :key="role.id" :label="role.name" :value="role.id"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="真实姓名" prop="realName">
            <el-input v-model.trim="form.realName" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item label="年龄" prop="age">
            <el-input v-model.number="form.age" placeholder="请输入年龄"></el-input>
          </el-form-item>
          <el-form-item label="性别" prop="gender">
            <el-select v-model="form.gender" placeholder="请选择性别">
              <el-option :label="'男'" :value="1"></el-option>
              <el-option :label="'女'" :value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="formVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 表格 -->
      <v-table :tableData="tableData" :columns="columns" :pagination="pagination" @handleTableChange="find" :operateColumn="operateColumn" :otherHeight="otherHeight"></v-table>
    </div>
    <div style="clear:both"></div>
  </div>

</template>
<script>
import VTable from '../components/VTable.vue'
import consts from '../assets/utils/consts.js'
const { RSA } = require('../assets/utils/rsa.js').crypto

export default {
  name: 'Student',
  components: {
    VTable,
  },
  data() {
    return {
      consts,
      passwordChangable: false,
      formVisible: false,
      specialityList: [],
      otherHeight: 0,
      search: {
        username: '',
        realName: '',
      },
      form: {
        username: '',
        password: '',
        userId: '',
        instituteId: '',
        specialityId: '',
        roleId: 3,
        realName: '',
        age: '',
        gender: '',
      },
      formRules: {
        username: { required: true, message: '请输入学号', trigger: 'blur' },
        password: {
          validator: (rule, value, callback) => {
            if (this.passwordChangable && !/^.*(?=.{6,})(?=.*\d)(?=.*[A-z!@#$%^&*,.]).*$/.test(value)) {
              callback(new Error('密码必须为6位以上数字和大小写字母组合！'))
            }
            callback()
          },
          trigger: 'blur',
        },
        age: { type: 'number', message: '年龄必须为数字值' },
      },
      columns: [
        { prop: 'username', label: '学号' },
        {
          prop: 'instituteId',
          label: '学院',
          format: row => `<span>${(row.instituteId && this.mapToInstitute(row.instituteId).name) || ''}</span>`,
        },
        {
          prop: 'specialityId',
          label: '专业',
          format: row =>
            `<span>${(row.specialityId && this.mapToSpeciality(row.instituteId, row.specialityId).name) || ''}</span>`,
        },
        { prop: 'realName', label: '姓名', width: '' },
        { prop: 'age', label: '年龄', width: '' },
        {
          prop: 'gender',
          label: '性别',
          format: row => `<span>${row.gender && row.gender === 1 ? '男' : '女' || ''}</span>`,
        },
      ],
      pagination: {
        current: 1,
        size: 10,
        total: 0,
        sidx: 1,
        sord: 'asc',
        roleId: 3,
      },
      tableData: [],
      operateColumn: {
        width: 150,
        fixed: 'right',
        list: [
          {
            type: 'warning',
            name: '编辑',
            method: row => {
              this.modify(row)
            },
          },
          {
            type: 'danger',
            name: '删除',
            method: row => {
              this.delete(row.userId)
            },
          },
        ],
      },
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
    usernamePrefix() {
      return this.form.instituteId + this.form.specialityId
    },
  },
  mounted() {
    this.find()
  },
  methods: {
    // 查
    find(pagination) {
      this.pagination = { ...this.pagination, ...pagination }
      this.$get('api/authority/user', this.pagination).then(({ data }) => {
        this.tableData = data.pageRecords
        this.pagination.total = data.recordAmount
      })
    },
    // 增
    add() {
      this.resetForm()
      this.formVisible = true
    },
    // 改
    modify(row) {
      this.initForm(row)
      this.formVisible = true
    },
    // 删
    delete(id) {
      this.$confirm('确定删除该学生?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.$delete(`api/authority/user/${id}`).then(() => {
            this.find()
          })
        })
        .catch(() => {})
    },
    sumitQuery() {
      this.find(this.search)
    },
    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.form.password && this.passwordChangable) {
            this.form.password = RSA.encrypt(this.form.password)
          }
          this.form.username = this.usernamePrefix + this.form.username
          this.$post('api/authority/user', this.form).then(() => {
            this.find()
            this.formVisible = false
          })
        }
        return valid
      })
    },
    resetForm() {
      this.passwordChangable = true
      this.$refs.form && this.$refs.form.clearValidate()
      Object.keys(this.form).forEach(key => {
        this.form[key] = ''
      })
      this.form.id = null
      this.form.roleId = 3
    },
    initForm(row) {
      this.passwordChangable = false
      this.$refs.form && this.$refs.form.clearValidate()
      this.changeSpeciality(row.instituteId)
      Object.keys(this.form).forEach(key => {
        this.form[key] = row[key]
      })
    },
    changeSpeciality(instituteId) {
      this.specialityList = this.mapToInstitute(instituteId).specialityList
      this.form.specialityId = ''
    },
    mapToRole(roleId) {
      return this.consts.roleList.find(item => item.id === roleId)
    },
    mapToInstitute(instituteId) {
      return this.consts.instituteList.find(item => item.id === instituteId)
    },
    mapToSpeciality(instituteId, specialityId) {
      return this.mapToInstitute(instituteId).specialityList.find(item => item.id === specialityId)
    },
  },
}
</script>

<style>
</style>



