import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import HeaderAsideLayout from '../layouts/HeaderAsideLayout'
import Dashboard from '../pages/Dashboard'
import UserLogin from '../pages/UserLogin'
import NotFound from '../pages/NotFound'
import Student from '../pages/Student.vue'
import Teacher from '../pages/Teacher.vue'
import Admin from '../pages/Admin.vue'
import Course from '../pages/Course.vue'
import CourseArrange from '../pages/CourseArrange.vue'
import CourseElection from '../pages/CourseElection.vue'
import CourseSchedule from '../pages/CourseSchedule.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      alias: '/dashboard',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: Dashboard
        }
      ]
    },
    {
      path: '/student',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: Student
        }
      ]
    },
    {
      path: '/teacher',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: Teacher
        }
      ]
    },
    {
      path: '/admin',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: Admin
        }
      ]
    },
    {
      path: '/course',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: Course
        }
      ]
    },
    {
      path: '/courseArrange',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: CourseArrange
        }
      ]
    },
    {
      path: '/courseElection',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: CourseElection
        }
      ]
    },
    {
      path: '/courseSchedule',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: CourseSchedule
        }
      ]
    },
    {
      path: '/login',
      component: UserLogin
    },
    {
      path: '*',
      component: HeaderAsideLayout,
      children: [
        {
          path: '',
          component: NotFound
        }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (store.state.user || to.path === '/login') {
    next()
  } else {
    next({ path: '/login' })
  }
})

export default router



