<template>
  <div id='main'>
    <div class="content">
      <!-- 搜索 -->
      <div class="choose">
        <div style="padding:15px 15px;">
          <el-form :inline="true" :model="search">
            <el-form-item label="课程名称">
              <el-input v-model="search.courseName" placeholder="请输入课程名称" clearable></el-input>
            </el-form-item>
            <el-form-item label="学院">
              <el-select v-model="search.instituteId" placeholder="请选择学院" clearable>
                <el-option v-for="item in consts.instituteList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="sumitQuery">查询</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!-- 表格 -->
      <v-table :tableData="tableData" :columns="columns" :pagination="pagination" @handleTableChange="find" :otherHeight="otherHeight"></v-table>
    </div>
    <div style="clear:both"></div>
  </div>

</template>
<script>
import VTable from '../components/VTable.vue'
import consts from '../assets/utils/consts.js'

export default {
  name: 'CourseElection',
  components: {
    VTable,
  },
  data() {
    return {
      consts,
      formVisible: false,
      otherHeight: 0,
      courseList: [],
      teacherList: [],
      search: {
        courseName: '',
        instituteId: '',
      },
      weekdayList: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日'],
      courseOrderList: ['第一大节', '第二大节', '第三大节', '第四大节'],
      form: {
        courseArrangeId: '',
        instituteId: '',
        courseId: '',
        userId: '',
        weekStart: '',
        weekEnd: '',
        weekday: [],
        courseOrder: '',
        classRoom: '',
      },
      columns: [
        {
          prop: 'courseId',
          label: '课程名称',
          format: row => `<span>${(row.course && row.course.courseName) || ''}</span>`,
        },
        {
          prop: 'userId',
          label: '授课教师',
          format: row => `<span>${(row.teacher && row.teacher.realName) || ''}</span>`,
        },
        {
          prop: 'instituteId',
          label: '所属学院',
          format: row => `<span>${(row.course && this.mapToInstitute(row.course.instituteId).name) || ''}</span>`,
        },
        {
          prop: 'specialityId',
          label: '所属专业',
          format: row =>
            `<span>${(row.course &&
              this.mapToMultipleSpecialityName(row.course.instituteId, row.course.specialityId)) ||
              ''}</span>`,
        },
        {
          prop: 'weekStart',
          label: '课程开始周次',
          format: row => `<span>第 ${row.weekStart} 周</span>`,
        },
        {
          prop: 'weekEnd',
          label: '课程结束周次',
          format: row => `<span>第 ${row.weekEnd} 周</span>`,
        },
        {
          prop: 'weekday',
          label: '课程天次',
          format: row =>
            `<span>${String(row.weekday)
              .split('0')
              .map(day => this.weekdayList[parseInt(day, 10) - 1])
              .join(',')}</span>`,
        },
        {
          prop: 'courseOrder',
          label: '当日课次',
          format: row => `<span>${this.courseOrderList[row.courseOrder - 1]}</span>`,
        },
        {
          prop: 'classRoom',
          label: '教室',
        },
        {
          prop: 'classRoom',
          label: '操作',
          render: row => {
            if (row.courseElection.userId === this.user.userId && row.courseElection.courseElectionId) {
              const tempUnselectCourse = () => {
                this.delete(row.courseElection.courseElectionId)
              }
              return [
                <el-tooltip content="退选" placement="top">
                  <el-button type="warning" onClick={tempUnselectCourse} icon="el-icon-circle-close-outline" circle />
                </el-tooltip>,
              ]
            }
            const tempSelectCourse = () => {
              this.selectCourse(row.courseArrangeId)
            }
            return [
              <el-tooltip content="选定" placement="top">
                <el-button type="success" onClick={tempSelectCourse} icon="el-icon-circle-plus-outline" circle />
              </el-tooltip>,
            ]
          },
        },
      ],
      pagination: {
        current: 1,
        size: 10,
        total: 0,
        sidx: 1,
        sord: 'asc',
        userId: '',
      },
      tableData: [],
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
  },
  mounted() {
    this.pagination.userId = this.user.userId
    this.find()
  },
  methods: {
    // 查
    find(pagination) {
      this.pagination = { ...this.pagination, ...pagination }
      this.$get('api/course/courseElection', this.pagination).then(({ data }) => {
        this.tableData = data.pageRecords
        this.pagination.total = data.recordAmount
      })
    },
    // 选
    selectCourse(courseArrangeId) {
      this.$confirm('确定选定课程?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.$post('api/course/courseElection', { courseArrangeId, userId: this.user.userId })
            .then((res) => {
              if (res) {
                this.find()
                this.$message.success('选定成功')
              }
            })
        })
        .catch(() => {})
    },
    // 删
    delete(id) {
      this.$confirm('确定退选课程?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.$delete(`api/course/courseElection/${id}`).then(() => {
            this.find()
            this.$message.success('退选成功')
          })
        })
        .catch(() => {})
    },
    sumitQuery() {
      this.find(this.search)
    },
    mapToRole(roleId) {
      return this.consts.roleList.find(item => item.id === roleId)
    },
    mapToInstitute(instituteId) {
      return this.consts.instituteList.find(item => item.id === instituteId)
    },
    mapToSpeciality(instituteId, specialityId) {
      return this.mapToInstitute(instituteId).specialityList.find(item => item.id === specialityId)
    },
    mapToMultipleSpecialityName(instituteId, specialityId) {
      return specialityId
        .split(',')
        .map(item => this.mapToSpeciality(instituteId, item).name)
        .join(',')
    },
    // findCourse(courseId) {
    //   return this.courseList.find(item => item.courseId === courseId)
    // },
    // fetchCourse(courseId) {
    //   const existCourse = this.findCourse(courseId)
    //   if (existCourse) {
    //     return existCourse
    //   }
    //   const tempCourse = { courseId, courseName: '' }
    //   this.courseList.push(tempCourse)
    //   this.$get('api/course/course', { courseId }).then(({ data }) => {
    //     this.findCourse(courseId).courseName = data.courseName
    //   })
    //   return tempCourse
    // },
  },
}
</script>

<style scoped>
</style>



