<template>
  <div id='main'>
    <div class="content">
      <!-- 搜索 -->
      <div class="choose">
        <div style="padding:15px 15px;">
          <el-form :inline="true" :model="search">
            <el-form-item label="课程名称">
              <el-input v-model="search.courseName" placeholder="请输入课程名称" clearable></el-input>
            </el-form-item>
            <el-form-item label="学院">
              <el-select v-model="search.instituteId" placeholder="请选择学院" clearable>
                <el-option v-for="item in consts.instituteList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="sumitQuery">查询</el-button>
            </el-form-item>
            <el-form-item v-if="user.roleId === 1">
              <el-button type="success" @click="add">新增</el-button>
            </el-form-item>
            <el-form-item label="选课截止时间" v-if="user.roleId === 1">
              <el-date-picker v-model="search.selectableDeadline" type="date" value-format="timestamp" placeholder="请选择截止日期" clearable></el-date-picker>
            </el-form-item>
            <el-form-item v-if="user.roleId === 1">
              <el-button type="success" @click="enableCourseElection">开启选课</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!-- 新增 -->
      <el-dialog :visible.sync="formVisible" title="排课" width="33%">
        <el-form ref="form" :model="form" :rules="formRules" label-width="120px">
          <el-form-item label="学院" prop="instituteId">
            <el-select v-model="form.instituteId" placeholder="请选择学院" @change="handleInstituteChange">
              <el-option v-for="item in consts.instituteList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="课程" prop="courseId">
            <el-select v-model="form.courseId" placeholder="请选择课程">
              <el-option v-for="item in courseList" :key="item.courseId" :label="item.courseName" :value="item.courseId"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="教师" prop="userId">
            <el-select v-model="form.userId" placeholder="请选择教师">
              <el-option v-for="item in teacherList" :key="item.userId" :label="item.realName" :value="item.userId"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="课程开始周次" prop="weekStart">
            <el-input-number v-model.number="form.weekStart" :min="1" :max="25" placeholder="请输入课程开始周次"></el-input-number>
          </el-form-item>
          <el-form-item label="课程结束周次" prop="weekEnd">
            <el-input-number v-model.number="form.weekEnd" :min="1" :max="25" placeholder="请输入课程结束周次"></el-input-number>
          </el-form-item>
          <el-form-item label="课程天次" prop="weekday">
            <el-select v-model="form.weekday" placeholder="请选择星期几上课" multiple>
              <el-option v-for="(item, index) in weekdayList" :key="index" :value="index + 1" :label="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="课程课次" prop="courseOrder">
            <el-select v-model="form.courseOrder" placeholder="请选择第几节课上课">
              <el-option v-for="(item, index) in courseOrderList" :key="index" :value="index + 1" :label="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="教室" prop="classRoom">
            <el-input v-model="form.classRoom" placeholder="请输入教室编号"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="formVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 查看学生表格 -->
      <el-dialog :visible.sync="studentListVisible" title="学生列表" width="50%">
        <v-table :tableData="studentList" :columns="studentColumns" :pagination="studentPagination" @handleTableChange="findStudentList"></v-table>
      </el-dialog>
      <!-- 表格 -->
      <v-table :tableData="tableData" :columns="columns" :pagination="pagination" @handleTableChange="find" :otherHeight="0"></v-table>
    </div>
    <div style="clear:both"></div>
  </div>

</template>
<script>
import VTable from '../components/VTable.vue'
import consts from '../assets/utils/consts.js'

export default {
  name: 'CourseArrange',
  components: {
    VTable,
  },
  data() {
    return {
      consts,
      formVisible: false,
      studentListVisible: false,
      courseList: [],
      teacherList: [],
      search: {
        courseName: '',
        instituteId: '',
        selectableDeadline: '',
      },
      weekdayList: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日'],
      courseOrderList: ['第一大节', '第二大节', '第三大节', '第四大节'],
      form: {
        courseArrangeId: '',
        instituteId: '',
        courseId: '',
        userId: '',
        weekStart: '',
        weekEnd: '',
        weekday: [],
        courseOrder: '',
        classRoom: '',
      },
      formRules: {
        courseId: { required: true, message: '请选择课程名', trigger: 'change' },
        userId: { required: true, message: '请选择教师', trigger: 'change' },
        weekStart: {
          required: true,
          type: 'number',
          message: '课程开始周次不正确',
          trigger: 'blur',
        },
        weekEnd: {
          required: true,
          validator: (rule, value, callback) => {
            if (value < this.form.weekStart) {
              callback('课程结束周次不正确')
            }
            callback()
          },
          message: '课程结束周次不正确',
          trigger: 'blur',
        },
        weekday: { required: true, message: '请选择星期几上课', trigger: 'blur' },
        courseOrder: { required: true, message: '请选择第几节课上课', trigger: 'blur' },
        classRoom: { required: true, message: '请输入教室编号', trigger: 'blur' },
      },
      columns: [
        {
          prop: 'courseId',
          label: '课程名称',
          format: row => `<span>${(row.course && row.course.courseName) || ''}</span>`,
        },
        {
          prop: 'userId',
          label: '授课教师',
          format: row => `<span>${(row.teacher && row.teacher.realName) || ''}</span>`,
        },
        {
          prop: 'instituteId',
          label: '所属学院',
          format: row => `<span>${(row.course && this.mapToInstitute(row.course.instituteId).name) || ''}</span>`,
        },
        {
          prop: 'specialityId',
          label: '所属专业',
          format: row =>
            `<span>${(row.course &&
              this.mapToMultipleSpecialityName(row.course.instituteId, row.course.specialityId)) ||
              ''}</span>`,
        },
        {
          prop: 'weekStart',
          label: '课程开始周次',
          format: row => `<span>第 ${row.weekStart} 周</span>`,
        },
        {
          prop: 'weekEnd',
          label: '课程结束周次',
          format: row => `<span>第 ${row.weekEnd} 周</span>`,
        },
        {
          prop: 'weekday',
          label: '课程天次',
          format: row =>
            `<span>${String(row.weekday)
              .split('0')
              .map(day => this.weekdayList[parseInt(day, 10) - 1])
              .join(',')}</span>`,
        },
        {
          prop: 'courseOrder',
          label: '当日课次',
          format: row => `<span>${this.courseOrderList[row.courseOrder - 1]}</span>`,
        },
        {
          prop: 'classRoom',
          label: '教室',
        },
        {
          prop: 'classRoom',
          label: '操作',
          render: row => {
            if (this.user.roleId === 1) {
              const tempDelete = () => {
                this.delete(row.courseArrangeId)
              }
              const tempEdit = () => {
                this.modify(row)
              }
              return [
                <el-tooltip content="编辑" placement="top">
                  <el-button type="primary" onClick={tempEdit} icon="el-icon-edit" circle />
                </el-tooltip>,
                <el-tooltip content="删除" placement="top">
                  <el-button type="danger" onClick={tempDelete} icon="el-icon-delete" circle />
                </el-tooltip>,
              ]
            }
            const tempFindStudentList = () => {
              this.studentListVisible = true
              this.findStudentList({ courseArrangeId: row.courseArrangeId })
            }
            return [
              <el-tooltip content="查看学生" placement="top">
                <el-button type="primary" onClick={tempFindStudentList} icon="el-icon-search" circle />
              </el-tooltip>,
            ]
          },
        },
      ],
      pagination: {
        current: 1,
        size: 10,
        total: 0,
        sidx: 1,
        sord: 'asc',
      },
      tableData: [],
      studentColumns: [
        { prop: 'username', label: '学号' },
        {
          prop: 'instituteId',
          label: '学院',
          format: row => `<span>${(row.instituteId && this.mapToInstitute(row.instituteId).name) || ''}</span>`,
        },
        {
          prop: 'specialityId',
          label: '专业',
          format: row =>
            `<span>${(row.specialityId && this.mapToSpeciality(row.instituteId, row.specialityId).name) || ''}</span>`,
        },
        { prop: 'realName', label: '姓名', width: '' },
      ],
      studentPagination: {
        current: 1,
        size: 10,
        total: 0,
        sidx: 1,
        sord: 'asc',
      },
      studentList: [],
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
  },
  mounted() {
    this.find()
  },
  methods: {
    enableCourseElection() {
      if (this.search.selectableDeadline) {
        this.$get('api/course/enableCourseElection', this.search)
          .then(res => {
            if (res) {
              this.search.selectableDeadline = ''
              this.$message.success('开启选课成功')
            } else {
              this.search.selectableDeadline = ''
              this.$message.error('开启选课失败')
            }
          })
      } else {
        this.$message.error('请选择截止日期')
      }
    },
    findStudentList(pagination) {
      this.studentPagination = { ...this.studentPagination, ...pagination }
      this.$get('api/course/findStudentListByCourseArrangeId', this.studentPagination).then(({ data }) => {
        this.studentList = data.pageRecords
        this.studentPagination.total = data.recordAmount
      })
    },
    // 查
    find(pagination) {
      this.pagination = { ...this.pagination, ...pagination }
      this.$get('api/course/courseArrange', this.pagination).then(({ data }) => {
        this.tableData = data.pageRecords
        this.pagination.total = data.recordAmount
      })
    },
    // 增
    add() {
      this.resetForm()
      this.formVisible = true
    },
    // 改
    modify(row) {
      this.initForm(row)
      this.formVisible = true
    },
    // 删
    delete(id) {
      this.$confirm('确定删除排课?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.$delete(`api/course/courseArrange/${id}`).then(res => {
            if (res) {
              this.find()
            }
          })
        })
        .catch(() => {})
    },
    sumitQuery() {
      this.find(this.search)
    },
    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$post('api/course/courseArrange', {
            ...this.form,
            weekday: parseInt(this.form.weekday.join('0'), 10),
          }).then(res => {
            if (res) {
              this.find()
              this.formVisible = false
            }
          })
        }
        return valid
      })
    },
    resetForm() {
      this.passwordChangable = true
      this.$refs.form && this.$refs.form.clearValidate()
      Object.keys(this.form).forEach(key => {
        this.form[key] = ''
      })
      this.form.weekday = []
    },
    initForm(row) {
      this.passwordChangable = false
      this.$refs.form && this.$refs.form.clearValidate()
      this.handleInstituteChange(row.instituteId)
      Object.keys(this.form).forEach(key => {
        this.form[key] = row[key]
      })
      this.form.instituteId = row.course.instituteId
      this.form.weekday = String(row.weekday)
        .split('0')
        .map(day => parseInt(day, 10))
    },
    handleInstituteChange(instituteId) {
      // 查找该学院课程
      this.form.courseId = ''
      this.form.userId = ''
      this.$get('api/course/course', { instituteId }).then(({ data }) => {
        this.courseList = data
      })
      // 查找该学院教师
      this.$get('api/authority/user', { instituteId, roleId: 2 }).then(({ data }) => {
        this.teacherList = data
      })
    },
    mapToRole(roleId) {
      return this.consts.roleList.find(item => item.id === roleId)
    },
    mapToInstitute(instituteId) {
      return this.consts.instituteList.find(item => item.id === instituteId)
    },
    mapToSpeciality(instituteId, specialityId) {
      return this.mapToInstitute(instituteId).specialityList.find(item => item.id === specialityId)
    },
    mapToMultipleSpecialityName(instituteId, specialityId) {
      return specialityId
        .split(',')
        .map(item => this.mapToSpeciality(instituteId, item).name)
        .join(',')
    },
    // findCourse(courseId) {
    //   return this.courseList.find(item => item.courseId === courseId)
    // },
    // fetchCourse(courseId) {
    //   const existCourse = this.findCourse(courseId)
    //   if (existCourse) {
    //     return existCourse
    //   }
    //   const tempCourse = { courseId, courseName: '' }
    //   this.courseList.push(tempCourse)
    //   this.$get('api/course/course', { courseId }).then(({ data }) => {
    //     this.findCourse(courseId).courseName = data.courseName
    //   })
    //   return tempCourse
    // },
  },
}
</script>

<style scoped>
</style>



