import * as types from './mutation-types'

export default {
  [types.LOG_IN](state, user) {
    state.user = user
  },
  [types.LOG_OUT](state) {
    state.user = ''
  },
  [types.UPDATE_USER](state, user) {
    state.user = { ...state.user, ...user }
  },
}



