<template>
  <el-menu class="navbar" mode="horizontal">
    <div class="user-profile-container" trigger="click">
      <div class="user-profile-content">
        <div class="menu-icons">
          <span class="menu-icon">
            <i class="el-icon-search icon"></i>
          </span>
          <span class="menu-icon">
            <i class="el-icon-message icon"></i>
          </span>
          <span class="menu-icon">
            <el-badge is-dot class="item">
              <i class="el-icon-bell icon"></i>
            </el-badge>
          </span>
        </div>
        <el-dropdown>
          <div class="user-profile-body">
            <img class="user-avatar" src="../../../assets/img/user-avatar.png">
            <span class="user-name">{{user.realName}}</span>
          </div>
          <el-dropdown-menu class="user-dropdown" slot="dropdown">
            <router-link to="/dashboard">
              <el-dropdown-item>我的主页</el-dropdown-item>
            </router-link>
            <el-dropdown-item>
              <span @click="formVisible = true" style="display:block;">修改密码</span>
            </el-dropdown-item>
            <el-dropdown-item>
              <span @click="logout" style="display:block;">退出</span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <el-dialog :visible.sync="formVisible" title="修改密码" width="33%">
      <el-form ref="form" :model="form" :rules="formRules" label-width="80px">
        <el-form-item label="原密码" prop="oldPassword">
          <el-input v-model="form.oldPassword" type="password" placeholder="请输入原密码"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="newPassword">
          <el-input v-model="form.newPassword" type="password" placeholder="请输入新密码"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="renewPassword">
          <el-input v-model="form.renewPassword" type="password" placeholder="请再次输入新密码"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="this.resetForm()">取 消</el-button>
        <el-button type="primary" @click="submitForm">确 定</el-button>
      </div>
    </el-dialog>
  </el-menu>
</template>

<script>
import * as types from '../../../store/mutation-types.js'
const { RSA } = require('../../../assets/utils/rsa.js').crypto

export default {
  name: 'NavBar',
  data() {
    return {
      formVisible: false,
      form: {
        oldPassword: '',
        newPassword: '',
        renewPassword: '',
      },
      formRules: {
        oldPassword: {
          required: true,
          pattern: /^.*(?=.{6,})(?=.*\d)(?=.*[A-z!@#$%^&*,.]).*$/,
          message: '原密码格式不正确',
        },
        newPassword: {
          required: true,
          pattern: /^.*(?=.{6,})(?=.*\d)(?=.*[A-z!@#$%^&*,.]).*$/,
          message: '新密码格式不正确',
        },
        renewPassword: {
          required: true,
          validator: (rule, value, callback) => {
            if (value !== this.form.newPassword) {
              callback(new Error('两次密码输入不一致'))
            }
            callback()
          },
          trigger: 'change',
        },
      },
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
  },
  methods: {
    logout() {
      this.$store.commit(types.LOG_OUT)
      this.$router.push({ path: '/login' })
    },
    submitForm() {
      this.$refs.form.validate(valid => {
        const params = {
          userId: this.user.userId,
          password: RSA.encrypt(this.form.oldPassword),
          newPassword: RSA.encrypt(this.form.newPassword),
        }
        if (valid) {
          this.$post('api/authority/modifyPassword', params).then(({ status, msg }) => {
            if (status === 200) {
              this.$message.success(msg || '修改成功')
              this.formVisible = false
            } else {
              this.resetForm()
              this.$message.error(msg || '修改失败')
            }
          })
        }
        return valid
      })
    },
    resetForm() {
      this.formVisible = true
      this.$refs.form && this.$refs.form.clearValidate()
      Object.keys(this.form).forEach(key => {
        this.form[key] = ''
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.navbar {
  height: 64px;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  .user-profile-container {
    position: absolute;
    right: 20px;
    cursor: pointer;
    .user-profile-content {
      display: flex;
      padding: 20px 0;
    }
    .menu-icons {
      display: flex;
      align-items: center;
      .menu-icon {
        padding: 0 12px;
        .icon {
          display: inline-block;
          font-size: 18px;
          text-align: center;
        }
      }
    }
    .user-profile-body {
      position: relative;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      text-align: center;
      padding-right: 14px;
    }
    .user-avatar {
      width: 24px;
      height: 24px;
      margin: 0 8px 0 12px;
      border-radius: 4px;
    }
    .user-name {
      color: rgba(0, 0, 0, 0.65);
    }
    .user-department {
      font-size: 12px;
      color: rgba(102, 102, 102, 0.65);
    }
    .el-icon-caret-bottom {
      position: absolute;
      right: 0;
      top: 13px;
      font-size: 12px;
    }
  }
}
</style>




