<template>
  <div class="table-wrap">
    <el-table id="vTable" v-loading.vTable="loading" border :data="computedTableData" :style="customStyle" :max-height="computedHeight" :row-class-name="tableRowClassName" @sort-change="handleSortChange" @row-click="rowClick">
      <!--region 表格-->
      <template v-for="(column, index) in columns">
        <el-table-column :key="index" :show-overflow-tooltip="column.showTooltip || true" :fixed="column.fixed" :sortable="column.sortable" :prop="column.prop" :type="column.type" :align="column.align" :label="column.label" :width="column.width" :min-width="column.minWidth || ''">
          <template slot-scope="scope">
            <template v-if="!column.render">
              <span v-if="!column.format">
                <span>{{scope.row[column.prop]}}</span>
              </span>
              <span v-else v-html="column.format(scope.row,column)" @click="column.method && column.method(scope.row,column)"></span>
            </template>
            <template v-else>
              <node-content :column="column" :row="scope.row"></node-content>
            </template>
          </template>
        </el-table-column>
      </template>
      <!--endregion-->
      <!--region 表格操作-->
      <el-table-column v-if="operateColumn" :fixed="operateColumn.fixed" label="操作" :width="operateColumn.width">
        <template slot-scope="scope">
          <template v-for="(op, index) in operateColumn.list">
            <el-button :key="index" :type="op.type" :icon="op.icon" :size="op.size || 'small'" v-if="!op.show || op.show(scope.row)" :disabled="op.disabled && op.disabled(scope.row)" @click="op.method(scope.row)">
              {{op.name}}
            </el-button>
          </template>
        </template>
      </el-table-column>
      <!--endregion-->
    </el-table>
    <!--分页-->
    <div class="block pagination" v-if="showPagination && !loading">
      <el-pagination :current-page="computedPagination.current" :page-size="computedPagination.size" :total="computedPagination.total" @current-change="handleCurrentChange" @size-change="handleSizeChange" layout="total, prev, pager, next, jumper">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  name: 'VTable',
  props: {
    tableData: Array, // 表格数据
    columns: Array, // 表头
    loading: Boolean, // 是否加表格loading
    customStyle: String, // 自定义样式
    pagination: {
      type: Object,
      default: null,
    }, // 分页
    operateColumn: {
      type: Object,
    }, // 操作集合
    otherHeight: {
      type: Number,
      default: 0,
    }, // 表格之外的高度
    showPagination: {
      type: Boolean,
      default: true,
    }, // 是否显示分页
    tableRowClassName: Function,
  },
  components: {
    NodeContent: {
      props: {
        column: {
          required: true,
        },
        row: {
          required: true,
        },
      },
      render(h) {
        return h('div', {}, this.column.render(this.row, this.column))
      },
    },
  },
  data() {
    return {
      tablePagination: {
        current: this.pagination && this.pagination.current || 1,
        size: this.pagination && this.pagination.size || 10,
        total: this.tableData ? this.tableData.length : 0,
        sidx: '',
        sord: '',
      },
    }
  },
  computed: {
    computedHeight() {
      const height = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
      return height - this.otherHeight
    },
    computedPagination() {
      if (this.pagination && this.pagination.current && this.pagination.size) {// 服务器控制分页
        return this.pagination
      }
      return this.tablePagination
    },
    computedTableData() {
      if (!this.showPagination) {
        return this.tableData
      }
      if (this.pagination && this.pagination.current && this.pagination.size) {// 服务器控制分页
        return this.tableData
      }
      if (this.tableData) {
        return this.tableData.slice(
          (this.tablePagination.current - 1) * this.tablePagination.size,
          this.tablePagination.current * this.tablePagination.size,
        )
      }
      return null
    },
  },
  methods: {
    handleCurrentChange(current) {
      document.getElementsByClassName('el-table__body-wrapper')[0].scrollTop = 0
      this.tablePagination.current = current
      if (this.pagination && this.pagination.current && this.pagination.size) {// 服务器控制分页
        this.$emit('handleTableChange', this.tablePagination)
      }
    },
    handleSortChange(sort) {
      document.getElementsByClassName('el-table__body-wrapper')[0].scrollTop = 0
      this.tablePagination.sidx = sort.prop
      this.tablePagination.sord = sort.order
      if (this.pagination && this.pagination.sidx && this.pagination.sord) {// 服务器控制排序
        this.$emit('handleTableChange', this.tablePagination)
      }
    },
    handleSizeChange(size) {
      document.getElementsByClassName('el-table__body-wrapper')[0].scrollTop = 0
      this.tablePagination.size = size
      if (this.pagination && this.pagination.current && this.pagination.size) {// 服务器控制分页
        this.$emit('handleTableChange', this.tablePagination)
      }
    },
    rowClick(row) {
      this.$emit('rowClick', row)
    },
  },
}
</script>
<style scoped>
.pagination {
  text-align: right;
  margin: 20px;
}

.el-table-column--selection .cell {
  padding-left: 0;
}
</style>



