<template>
  <div class="user-login">
    <div class="user-login-bg"></div>
    <div class="content-wrapper">
      <h2 class="slogan">
        欢迎使用 <br /> 选课系统
      </h2>
      <div class="form-container">
        <h4 class="form-title">登录</h4>
        <el-form ref="form" :model="user" label-width="10px">
          <div class="form-items">
            <el-row class="form-item">
              <el-col>
                <el-form-item prop="username" :rules="[{required: true, message: '学号不能为空'}]">
                  <div class="form-line">
                    <i class="el-icon-edit-outline input-icon"></i>
                    <el-input placeholder="学号" v-model="user.username"></el-input>
                  </div>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row class="form-item">
              <el-col>
                <el-form-item prop="password" :rules="[{trigger: 'blur', required: true, pattern: /^.*(?=.{6,})(?=.*\d)(?=.*[A-z!@#$%^&*,.]).*$/, message: '密码必须为6位以上数字和大小写字母组合！'}]">
                  <div class="form-line">
                    <!-- <i class="el-icon-service input-icon"></i> -->
                    <svg-icon :icon-class="'bug'" />
                    <el-input type="password" placeholder="密码" v-model="user.password" @keypress.enter.native="submitLogin"></el-input>
                  </div>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row class=form-item>
              <el-col>
                <el-form-item>
                  <el-checkbox v-model="rememberMe" class="checkbox">记住账号</el-checkbox>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row class="form-item">
              <el-button type="primary" class="submit-btn" size="small" @click="submitLogin">
                登 录
              </el-button>
            </el-row>
          </div>
          <!-- <el-row class="tips">
            <span class="line">|</span>
            <a href="/" class="link">
              忘记密码
            </a>
          </el-row> -->
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import Cookie from 'js-cookie'
const RSA = require('../../assets/utils/rsa').crypto.RSA

export default {
  name: 'UserLogin',
  data() {
    return {
      rememberMe: false,
      user: {
        username: '',
        password: '',
      },
    }
  },
  mounted() {
    this.user.username = Cookie.get('username') || ''
  },
  methods: {
    submitLogin() {
      this.$refs.form.validate(valid => {
        // 将用户名存储在cookie中
        if (this.rememberMe) {
          Cookie.set('username', this.user.username, { expires: 7, path: '' })
        }
        if (valid) {
          this.$post('api/authority/login', {
            username: this.user.username,
            password: RSA.encrypt(this.user.password),
          })
            .then(({ status, msg, data }) => {
              if (status === 200) {
                this.$store.dispatch('login', data)
                this.$router.push({ path: '/dashboard' })
                this.$message({ message: '登录成功', type: 'success' })
              } else {
                this.user.password = ''
                this.$message({ message: msg || '登录失败', type: 'error' })
              }
            })
        }
      })
    },
  },
}
</script>

<style lang="scss" scoped>
@import './UserLogin.scss';
</style>



