<template>
  <div id="main">
    <div class="cv-wrapper locale-zh locale-zh-cn y2018 m05 period-month periodCount-1 theme-default holiday-us-traditional holiday-us-official">
      <div class="cv-header">
        <div class="periodLabel">课程计划</div>
      </div>
      <div class="cv-header-days">
        <div class="cv-header-day dow1">周一</div>
        <div class="cv-header-day dow2">周二</div>
        <div class="cv-header-day dow3">周三</div>
        <div class="cv-header-day dow4">周四</div>
        <div class="cv-header-day dow5">周五</div>
        <div class="cv-header-day dow6">周六</div>
        <div class="cv-header-day dow0">周日</div>
      </div>
      <div class="cv-weeks">
        <div v-for="(row, i) in table" :key="i" class="cv-week">
          <div v-for="(cell, j) in row" :key="j" class="cv-day">
            <div v-for="(item, k) in cell.data" :key="k" class="cv-day-number">
              <v-node :info="item.info" :period="item.period"></v-node>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'CourseSchedule',
  components: {
    VNode: {
      props: {
        info: {
          required: true,
        },
        period: {
          required: true,
        },
      },
      render(h) {
        return h('div', {}, [this.info, this.period])
      },
    },
  },
  data() {
    return {
      table: [
        [{ data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }],
        [{ data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }],
        [{ data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }],
        [{ data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }, { data: [] }],
      ],
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
  },
  mounted() {
    this.find(this.user.roleId === 2 ? 'courseArrange' : 'courseElection')
  },
  methods: {
    find(urlSuffix) {
      this.$get(`api/course/${urlSuffix}`, { userId: this.user.userId }).then(({ data }) => {
        data
          .filter(item => item.userId === this.user.userId)
          .forEach(({ course, teacher, courseOrder, weekday, weekStart, weekEnd }) => {
            const cols = String(weekday)
              .split('0')
              .map(item => parseInt(item, 10))
            cols.forEach(col => {
              this.table[courseOrder - 1][col - 1].data.push({
                info: (
                  <el-tag size="mini" type="success">
                    {course.courseName} - {teacher.realName}
                  </el-tag>
                ),
                period: (
                  <el-tag size="mini" type="danger">
                    ({weekStart}~{weekEnd}周)
                  </el-tag>
                ),
              })
            })
          })
      })
    },
  },
}
</script>

<style scoped>
.cv-header .periodLabel {
  display: flex;
  flex: 1 1 auto;
  flex-flow: row nowrap;
  min-height: 1.5em;
  line-height: 1;
  font-size: 1.5em;
  padding: 2rem 2rem;
}
.cv-wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
  min-height: 100%;
  max-height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}
.cv-wrapper,
.cv-wrapper div {
  box-sizing: border-box;
  font-size: 1em;
}
.cv-header-days {
  flex-grow: 0;
  flex-basis: auto;
  border-width: 0 0 0 1px;
}
.cv-header-day,
.cv-header-days {
  display: flex;
  flex-shrink: 0;
  flex-flow: row nowrap;
}
.cv-header-day {
  flex-grow: 1;
  flex-basis: 0;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-width: 1px 1px 0 0;
}
.cv-weeks {
  flex-shrink: 1;
  flex-basis: auto;
  flex-flow: column nowrap;
  border-width: 0 0 1px 1px;
}
.cv-week,
.cv-weeks {
  display: flex;
  flex-grow: 1;
  overflow-y: auto;
  -ms-overflow-style: none;
}
.cv-week {
  flex-flow: row nowrap;
  min-height: 3em;
  border-width: 0;
  position: relative;
  width: 100%;
}
.cv-day,
.cv-week {
  flex-shrink: 0;
  flex-basis: 0;
}
.cv-day {
  display: inline;
  flex-grow: 1;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  border-width: 1px 1px 0 0;
  overflow-y: auto;
}
.cv-day-number {
  text-align: center;
}
.cv-day,
.cv-header-day,
.cv-header-days,
.cv-week,
.cv-weeks {
  border-style: solid;
  border-color: #ddd;
}
.cv-day-number,
.cv-header-day {
  padding: 0.2em;
}
.cv-day-number:before {
  margin-right: 0.5em;
}
.cv-week::-webkit-scrollbar,
.cv-weeks::-webkit-scrollbar {
  width: 0;
  background: transparent;
}
.cv-week {
  min-height: 8em;
}
</style>



