<template>
  <div class="dashboard">
    <div class="draggable-header-view">
      <div class="header">
        <div class="avatar-wrapper">
          <el-upload class="avatar-uploader" :action="consts.api+'/authority/uploadAvatar'" :headers="{token: user.token}" :data="{userId: user.userId}" :show-file-list="false" :on-error="handleAvatarError" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
            <img v-if="user.avatar" :src="user.avatar" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </div>
      </div>
      <el-alert v-if="!editable" title="个性说明" type="info" :description="user.desc" :closable="false" show-icon></el-alert>
      <el-input v-else v-model="form.desc" type="textarea" autosize placeholder="请输入个性说明" clearable></el-input>
    </div>
    <div class="profile-user-info profile-user-info-striped">
      <div class="profile-info-row" style="text-align: right;">
        <el-button v-if="!editable" type="primary" @click="editable = true" icon="el-icon-edit"></el-button>
        <template v-else>
          <el-button type="success" @click="submitForm" icon="el-icon-circle-check"></el-button>
          <el-button type="warning" @click="editable = false" icon="el-icon-circle-close"></el-button>
        </template>
      </div>
      <div class="profile-info-row">
        <div class="profile-info-name"> 学院 </div>
        <div class="profile-info-value">
          <span class="editable editable-click" id="username"> {{user.instituteName}} </span>
        </div>
      </div>
      <div class="profile-info-row">
        <div class="profile-info-name"> 身份 </div>
        <div class="profile-info-value">
          <span class="editable editable-click"> {{mapToRole(user.roleId).name}} </span>
        </div>
      </div>
      <div class="profile-info-row">
        <div class="profile-info-name"> 真实姓名 </div>
        <div class="profile-info-value">
          <el-input v-if="editable" v-model="form.realName" placeholder="请输入个性说明" class="profile-edit-input" clearable></el-input>
          <span v-else class="editable editable-click"> {{user.realName}} </span>
        </div>
      </div>
      <div class="profile-info-row">
        <div class="profile-info-name"> 年龄 </div>
        <div class="profile-info-value">
          <el-input v-if="editable" v-model="form.age" placeholder="请输入个性说明" class="profile-edit-input" clearable></el-input>
          <span v-else class="editable editable-click"> {{user.age}} </span>
        </div>
      </div>
      <div class="profile-info-row">
        <div class="profile-info-name"> 性别 </div>
        <div class="profile-info-value">
          <el-select v-if="editable" v-model="form.gender" placeholder="请选择性别" class="profile-edit-input" clearable>
            <el-option :value="1" :label="'男'"></el-option>
            <el-option :value="0" :label="'女'"></el-option>
          </el-select>
          <span v-else class="editable editable-click"> {{user.gender ? '男': '女'}} </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import consts from '../../assets/utils/consts.js'
import * as types from '../../store/mutation-types.js'

export default {
  data() {
    return {
      consts,
      editable: false,
      form: {
        userId: '',
        realName: '',
        age: '',
        gender: '',
        desc: '',
      },
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
  },
  mounted() {
    this.form.userId = this.user.userId
    this.form.realName = this.user.realName
    this.form.age = this.user.age
    this.form.gender = this.user.gender
    this.form.desc = this.user.desc
  },
  methods: {
    handleAvatarSuccess(res) {
      this.$store.commit(types.UPDATE_USER, { avatar: res.data })
    },
    handleAvatarError() {
      this.$router.push({ path: '/login' })
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    mapToRole(roleId) {
      return this.consts.roleList.find(item => item.id === roleId)
    },
    submitForm() {
      if (this.validateForm()) {
        this.editable = false
        this.$post('api/authority/user', this.form)
          .then(() => {
            this.$store.commit(types.UPDATE_USER, this.form)
            this.$message.success('修改信息成功!')
          })
          .catch(() => {
            this.$message.error('修改信息失败!')
          })
      } else {
        this.$message.error('提交信息不完整，请核验')
      }
    },
    validateForm() {
      let flag = true
      Object.keys(this.form).forEach(key => {
        flag = flag && !/^\s*$/.test(key)
      })
      flag = flag && /^\d{2}$/.test(this.form.age)
      return flag
    },
  },
}
</script>

<style scoped>
.dashboard {
  display: flex;
}
.draggable-header-view {
  flex: 1;
  background-color: #fff;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
  width: 320px;
  height: 830px;
  overflow: hidden;
  margin: 30px auto;
  position: relative;
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.header {
  height: 230px;
  padding: 20px 20px;
  background-color: #efecec;
}
.avatar-wrapper {
  text-align: center;
  border: 1px dashed #98b6ce;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin: 0 auto;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  width: 100%;
}
.profile-user-info {
  flex: 4;
  margin: 30px 12px;
  text-align: center;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.profile-info-row {
  position: relative;
  line-height: 50px;
}

.profile-info-name {
  position: absolute;
  width: 400px;
  text-align: right;
  padding: 6px 10px 6px 0;
  left: 0;
  top: 0;
  bottom: 0;
  font-weight: normal;
  color: #667e99;
  background-color: transparent;
  border-top: 1px dotted #d5e4f1;
}

.profile-edit-input {
  width: 12rem;
}

.profile-info-value {
  padding: 6px 4px 6px 6px;
  margin-left: 120px;
  border-top: 1px dotted #d5e4f1;
}

.profile-info-value > span + span:before {
  display: inline;
  content: ',';
  margin-left: 1px;
  margin-right: 3px;
  color: #666;
  border-bottom: 1px solid #fff;
}

.profile-info-value > span + span.editable-container:before {
  display: none;
}

.profile-info-row:first-child .profile-info-name {
  border-top: 0;
}

.profile-info-row:first-child .profile-info-value {
  border-top: 0;
}

.profile-user-info-striped {
  border: 1px solid #dcebf7;
}

.profile-user-info-striped .profile-info-name {
  color: #336199;
  background-color: #edf3f4;
  border-top: 1px solid #f7fbff;
}

.profile-user-info-striped .profile-info-value {
  border-top: 1px dotted #dcebf7;
  padding-left: 12px;
}

.profile-picture {
  border: 1px solid #ccc;
  background-color: #fff;
  padding: 4px;
  display: inline-block;
  max-width: 100%;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
}

img.editable-click {
  border: 1px dotted #bbb;
}

.editable-click:hover {
  border-color: #08c;
  color: #08c;
}

img.editable-click:hover {
  opacity: 0.75;
  filter: alpha(opacity=75);
}
</style>




